button {
  background: #19aaad;
  color: #fff;
  border: none;
  position: relative;
  height: 30px;
  font-size: 17px;
  padding: 0 20px;
  cursor: pointer;
  -webkit-transition: 800ms ease all;
  transition: 800ms ease all;
  outline: none;
}
button:hover {
  background: #fff;
  color: #000;
}
button:before,
button:after {
  content: '';
  position: absolute;
  top: 0;
  right: 0;
  height: 2px;
  width: 0;
  background: #19aaad;
  -webkit-transition: 400ms ease all;
  transition: 400ms ease all;
}
button:after {
  right: inherit;
  top: inherit;
  left: 0;
  bottom: 0;
}
button:hover:before,
button:hover:after {
  width: 100%;
  -webkit-transition: 800ms ease all;
  transition: 800ms ease all;
}
